<template>
    <div class="income">
        <nav-bar
            title="我的现金"
            :hairline="false" />
        <income-board
            :my-coin="income.myCoin"
            :equivalent-cash="income.equivalentCash"
            :today-coin="income.todayCoin"
            :cumulative-coin="income.cumulativeCoin"
            :go-withdraw-handler="goWithdrawHandler"
        />
        <income-container
            class="income__record"
            title="金币收益">
            <template slot="desc">近{{ income.showHistoryDay }}天</template>
            <record-list
                empty-text="暂无金币收益"
                :list="incomeList"
                :list-loading="listLoading"
                :finished="finished"
                :load-more="getIncome"
            >
                <income-list
                    slot="list"
                    :income-list="incomeList" />
                <Button
                    v-if="showCoinBtn"
                    class="empty-btn"
                    slot="empty-btn"
                    @click="goMineTabTask">去赚金币</Button>
            </record-list>
        </income-container>
        <div
            class="footer-income"
            @click="goLottery">
            <img
                :src="footerBanner.imgUrl"
                alt >
        </div>
    </div>
</template>

<script>
import { openCommonWebViewByPath } from '@/utils/jsApiV2/client';
import { goMineTabTask } from '@/utils/jsApiV2/deeplink';
import config from '@/config';
import NavBar from '@/components/NavBar';
import Button from '@/components/Button';
import RecordList from '@/containers/RecordList';
import { get } from '@/api/income';
import { addCustomEventListener } from '@/utils/event';
import { getConfigByKey } from '@/api/common';
import IncomeBoard from './IncomeBoard';
import IncomeContainer from './IncomeContainer';
import IncomeList from './IncomeList';
import { apolloIncomeFooterBanner, defaultFooterBanner } from './config';

export default {
    name: 'Income',
    components: {
        NavBar,
        Button,
        IncomeBoard,
        RecordList,
        IncomeContainer,
        IncomeList,
    },
    data() {
        return {
            footerBanner: {
                link: '',
                title: '',
                imgUrl: '',
            },
            listLoading: false,
            finished: false,
            income: {},
            incomeList: [],
            page: 1,
        };
    },
    computed: {
        showCoinBtn() {
            return config.versionCode >= 40605;
        },
    },
    created() {
        this.getIncome()
            .then(this.$root.hidePlaceholder)
            .catch(this.$error);

        // 从阿波罗配置中获取底部动态图和跳转链接
        this.getApolloIncomeFooterBanner();

        addCustomEventListener('focus', () => {
            this.getIncome();
        });
    },
    methods: {
        goMineTabTask,
        getApolloIncomeFooterBanner() {
            getConfigByKey({
                key: apolloIncomeFooterBanner,
            })
                .then((res) => {
                    const footerBannerConfig = res.data.incomeIndex;
                    const randomIndex = parseInt(Math.random() * footerBannerConfig.length, 10);
                    this.footerBanner = footerBannerConfig[randomIndex];
                    this.$o2o({
                        e_c: '任务',
                        e_a: '曝光',
                        e_n: '我的金币曝光',
                        e_x: JSON.stringify({
                            name: this.footerBanner.title,
                        }) });
                })
                .catch(() => {
                    // 当阿波罗配置报错或者超时 给定默认值
                    this.footerBanner = defaultFooterBanner;
                    this.$o2o({
                        e_c: '任务',
                        e_a: '曝光',
                        e_n: '我的金币曝光',
                        e_x: JSON.stringify({
                            name: this.footerBanner.title,
                        }) });
                });
        },
        getIncome() {
            return get({ page: this.page })
                .then((data) => {
                    if (this.page === 1) {
                        this.income = data;
                    }

                    this.incomeList = [...this.incomeList, ...data.recordList];
                    this.loading = false;
                    this.listLoading = false;
                    this.page += 1;

                    this.finished = !data.hasMore;
                })
                .catch(() => Promise.reject());
        },
        goWithdrawHandler() {
            openCommonWebViewByPath('/mobile-v2/withdraw');
            this.$o2o({ e_c: '任务', e_a: '点击', e_n: '去提现' });
        },
        goLottery() {
            openCommonWebViewByPath(`${this.footerBanner.link}`);
            this.$o2o({
                e_c: '任务',
                e_a: '点击',
                e_n: '我的金币底部按钮点击',
                e_x: JSON.stringify({
                    name: this.footerBanner.title,
                }),
            });
        },
    },
};
</script>

<style scoped>
.income {
    display: flex;
    flex-direction: column;
    height: 100%;
}

.income >>> .income__board {
    flex-shrink: 0;
}

.income__record {
    display: flex;
    flex-direction: column;
    flex: 1;
    overflow: hidden;
    padding: 0 17px;
    font-size: 15px;
    margin-top: -20px;
    border-radius: 20px 20px 0 0;
    background: #fff;
}

.empty-btn.button {
    width: auto;
    height: auto;
    padding: 6px 25px;
    font-size: 15px;
    font-weight: bold;
    background-color: #eb4940;
}

.income__record >>> .record__list-list,
.income__record >>> .record__list-empty {
    padding-bottom: 73px;
}

.footer-income {
    width: 100%;
    height: 73px;
    position: fixed;
    bottom: 0;
}

.footer-income img {
    height: 73px;
    width: 100%;
    position: absolute;
}
</style>
